import React from "react";
import s from "./Header.module.css";
import logo from "./../../img/logo.png";
import { useNavigate } from "react-router-dom";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import { Avatar, Container, Menu, MenuItem, Tooltip } from "@mui/material";
import { Link as RouterLink } from "react-router-dom";
import { Link as MaterialLink } from "@mui/material";
import { AccountCircle } from "@mui/icons-material";
import noAvatar from "./../../img/user_wall.png";

const Header = (props) => {
  const [auth, setAuth] = React.useState(true);
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleChange = (event) => {
    setAuth(event.target.checked);
  };

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const navigate = useNavigate();

  return (
    // <header className={s.header}>
    //   <div className={s.header__wrapper}>
    //     <div className={s.header__left}>
    //       <div className={s.header__logo}>
    //         <Link to="/">
    //           <img src={logo} alt="" />
    //         </Link>
    //       </div>
    //       <div className={s.header__title}>
    //         <Link to="/">My social network...</Link>
    //       </div>
    //     </div>
    //     <div className={s.header__right}>
    //       <div className={s.header__login}>
    //         {props.auth.isAuth === true ? (
    //           <>
    //             <div>Hello, {props.auth.login}</div>
    //             <div>
    //               <button onClick={props.logoutUserFromSite}>Log out</button>
    //             </div>
    //           </>
    //         ) : (
    //           <Link to="/login">Please login...</Link>
    //         )}
    //       </div>
    //     </div>
    //   </div>
    // </header>

    <AppBar position="static">
      <Container maxWidth="lg">
        <Toolbar disableGutters sx={{ display: "flex", justifyContent: "space-between" }}>
          <Box sx={{ display: "flex", flexDirection: "row", alignItems: "center" }}>
            <Box sx={{}}>
              <MaterialLink
                component={RouterLink}
                variant="h6"
                noWrap
                sx={{
                  mr: 2,
                  fontWeight: 700,
                  color: "inherit",
                  textDecoration: "none",
                }}
                to="/"
              >
                <img src={logo} alt="Logo" />
              </MaterialLink>
            </Box>

            <MaterialLink
              component={RouterLink}
              variant="h6"
              noWrap
              sx={{
                mr: 2,
                fontWeight: 700,
                color: "inherit",
                textDecoration: "none",
              }}
              to="/"
            >
              My social network
            </MaterialLink>
          </Box>
          <Box>
            {props.auth.isAuth === true ? (
              <Box sx={{ display: "flex", alignItems: "center" }}>
                <Box sx={{ flex: "1 0 auto" }}>Hello, {props.auth.login}</Box>
                <Tooltip title="Open settings">
                  <IconButton sx={{ pl: "20px", width: "50px" }} onClick={handleMenu}>
                    <Avatar alt="User avatar" src={props.avatar ? props.avatar : noAvatar} />
                  </IconButton>
                </Tooltip>
                <Menu
                  id="menu-appbar"
                  anchorEl={anchorEl}
                  anchorOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  keepMounted
                  transformOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  open={Boolean(anchorEl)}
                  onClose={handleClose}
                >
                  <MenuItem
                    onClick={() => {
                      navigate("/profile");
                      handleClose();
                    }}
                  >
                    Profile
                  </MenuItem>
                  <MenuItem
                    onClick={() => {
                      props.logoutUserFromSite();
                      handleClose();
                    }}
                  >
                    Log out
                  </MenuItem>
                </Menu>
              </Box>
            ) : (
              <Box sx={{ flex: "1 0 auto" }}>
                <MaterialLink
                  component={RouterLink}
                  noWrap
                  sx={{
                    mr: 2,
                    color: "inherit",
                    textDecoration: "none",
                  }}
                  to="/login"
                >
                  Please login...
                </MaterialLink>
              </Box>
            )}
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Header;
